body {
  min-width: 320px;
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;
  margin: 0;
  padding: 30px;
  position: relative;
}

.drag-area, .draggable-item, .dropped-item {
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 1px solid #000;
}

.draggable-item {
  z-index: 1;
}

.draggable-item_hidden {
  display: none;
}

.draggable-item, .dropped-item_canvas {
  position: absolute;
}

.container {
  column-gap: 30px;
  margin-top: 30px;
  display: flex;
}

.drop-area {
  box-sizing: border-box;
  width: calc(50% - 15px);
  aspect-ratio: 1 / 1;
  border: 1px dashed #000;
  overflow: auto;
}

.drop-area_highlighted {
  background-color: #ededed;
}

.drop-area_grid {
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.drop-area_canvas {
  position: relative;
}

